.contactData {
    margin: 25px 10%;
    width: 80%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
    float: left;
}

@media (min-width: 600px) {
    .contactData {
        width: 47%;
        margin: 25px 0%;
    }
}