.buildControls {
    width: 100%;
    background-color: #CF8F2E;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 40px 0;
    height: auto;
    overflow: hidden;
}

.orderButton {
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #966909;
}

.orderButton:hover, .orderButton:active {
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

.orderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.orderButton:not(:disabled) {
    animation: enable 0.3s linear;
}

@media (min-width: 600px) {
    .buildControls {
        width: 46%;
        height: 425px;
        float: left;
        margin: 30px auto;
    }
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}